body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.title{
  padding:9px 70px;
  background-color: black;
  color: white;
  display:flex;
  font-size: 30px;
  font-weight: bold;
  letter-spacing: 2px;
  justify-content: space-between;
}

.header{
  padding:20px 70px;
  background-color: black;
  color: white;
  display:flex;
  font-weight: bold;
  justify-content: space-between;
}
.cart-icon .counter{
  height: 18px;
  width:18px;
  background-color: blue;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 7px;
  top:-6px;
  position: relative;
}
.cart-icon{
  display: flex;
}
.icon{
  font-size: 40px;
  right:2px;
  position: relative;
}
img{
  height:150px;
}
.card-wrapper{
  display:inline-flex;
  
  padding:20px ;
  box-shadow: 0px 0px 7px black;
  width:320px;
  border-radius: 5px;
  gap:10px;
}
.image-wrapper{
  width:180px ;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid rgb(201,201,201);
}
.content{
  display:flex;
  flex-direction: column;
  flex-grow: 1;
  gap:10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 1px solid ;
}
.quantity div{
  height: 22px;
  width: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity >div:first-child,.quantity>button:last-child{
  background-color: rgb(0,0,111);
  color:white;
  border-radius: 0;
}
.quantity>button:disabled{
  opacity:0.7;
}
main{
  padding:20px;
  display:flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
  
}
.total{
  background-color: pink;
  padding: 17px 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: right;
  padding-right: 20px;
  box-sizing: border-box;
  font-size: 20px;
  font-weight: bold;
}